.task{
    /* margin: 5px 0; */
    padding: 2.5px 0;
    /* height: 100vh; */
}

.taskDiv{
    display: grid;
    grid-template-columns: 1fr 100px;

}
.taskDiv h5{
    align-self: center;
    text-align: left;
    word-break:break-all;
    padding-right: 20px;
    cursor: pointer;
    user-select: none;
    color: rgb(97, 218, 251);
}


.taskDiv h5[data-complete='true']{
    color: rgba(97, 218, 251, 0.336);   
    text-decoration:line-through;
  }

