@import url('https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Comfortaa', cursive;
  /* color: rgb(12, 12, 13); */
}
body{
  /* background-color: #270092; */
  background-color: #282c34;
  height: 100%;
  overflow-y: hidden;
}
.App {
  max-width: 600px;
  /* height: 100vh; */
  /* height: 100%; */
  text-align: center;
  display: grid;
  grid-template-columns: 83px 1fr;
  font-size: 18px;
  color: white;
  /* padding: 20px; */
  margin: 0 auto;
  /* margin-top: 20px; */
  /* padding-top: 20px; */
  border-left: 1px solid rgba(97, 218, 251, 0.1);
  border-right: 1px solid rgba(97, 218, 251, 0.1);
}
.taskBlock{
  margin-top: 20px;
}
.taskBlockWrap{
  /* height: 100vh; */
  /* height: 100%; */
  /* background:rgb(245, 132, 31); */
  overflow-y: scroll;
  scrollbar-width: none;
  position: relative;
  margin-top: 57px;
  /* padding: 57px 0 20px; */
  padding: 0 0 20px;
}
.taskBlockWrap::-webkit-scrollbar {
  display: none;
}
.taskBlockInputAndButtons{
  top: 0;
  /* right: 0;
  left: 0; */
  /* width: calc(100% - 83px); */
  width: calc(100% - 83px);
  max-width: 516px;
  /* width: 516px; */
  position: fixed;
  background:rgb(40, 44, 52);
  padding-bottom: 5px;
}

.emojiTicker{
  font-size: 20px;
  width: 30px;
  display: inline-block;
  text-align: center;

}

.buttonTask{
  /* margin-top: 5px; */
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
}
.CategorySmile{
  /* width: 82px; */
  width: 100%;
  height: 50px;
  /* font-size: 15px; */
  /* border: 2px solid rgb(97, 218, 251); */
  /* display: flex;
  align-items: center;
  justify-content: center; */
  display: grid;
  grid-template-columns: 20px 1fr;
  /* display: flex;
  justify-content: space-between; */
  padding: 0 5px;
  /* border: 1px solid black; */

}
.CategorySmile p{
  font-size: 25px;
  user-select: none;
  cursor: pointer;
}
.CategorySmile p:hover{
  opacity: 0.5;
}
.classesBlock{
  margin-top: 50px;
  padding-bottom: 20px;
  /* border-right: 5px solid rgba(97, 218, 251, 0); */
  border-right: 1px solid rgb(97, 218, 251, 0.1);
  overflow-x: hidden;
  overflow-y: scroll;
  scrollbar-width: none;
  position: relative;
}

.classesBlockNavbar{
  position: fixed;
  top: 0;
  background-color: #282c34;
}
.classesBlock::-webkit-scrollbar {
  display: none;
}

.CategorySmile a{
  text-decoration: none;
}
.CategorySmile a:active{
  text-decoration: none;
}
.CategorySmile a:hover{
  text-decoration: none;
}
.CategorySmile a:link{
  text-decoration: none;
}
.CategorySmile a:visited{
  text-decoration: none;
}
.buttonDelete{
  padding: 5px;
  background: none;
  /* border: 1px solid black; */
  border: none;
  cursor: pointer;
  color: rgb(255, 82, 82);
  align-self: center;
  text-align: center;
  margin: 0 auto;
  width: 20px;
}
.CategorySmileLink{
  align-self: center;
  text-align: center;
  margin: 0 auto;

}
